.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input[type=button]:hover , .selectedButton{
  background-color: #dfdfdf !important;
  border-color: #c7c7c7 !important;
}
input[type=button]{
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  background-color: #f3f3f3;
  padding: 0;
  cursor: pointer;
}
.topNav{
  left: 14px;
  top:14px;
  right:14px;
  height: 50px;
  padding:0;
  margin:0;
  position: fixed;
  display: flex;
  justify-content: space-between;
}
.topNav input[type=button]{
  width: 100%;
  height: 50px;
}
.topNav li{
  width: 32%;
  display: inline;
}
.leftNav{
  width:200px;
  left:14px;
  top:100px;
  bottom: 14px;
  position: fixed;
  padding:0;
  margin:0;
  list-style-type: none;
}
.leftNav input[type=button]{
  width: 100%;
  height: 50px;
  margin-bottom: 12px;
}
.view{
  position: fixed;
  left: 228px;
  right: 14px;
  top:100px;
  bottom: 14px;
}
.noNav{
  left: 14px;
}
.noNav th, td{
  padding: 12px 24px !important;
  text-align: left
}
.noNav :is(.google-visualization-table , table){
  width: 100%;
}
.noNav input[type=text]{
  display: block;
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  background-color: #f3f3f312;
  padding: 14px;
  margin-bottom: 25px;
  width: 300px;
  max-width: 100%;
  float: right;
}